import PropTypes from "prop-types"
import React from "react"
import styled from 'styled-components'


const CvSectionItem = ({ item }) => {
  const { title, description, date, details } = item;

  const Section = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
  `
  var sectionBody = '';

  if (title === undefined) { //tech sections
    sectionBody = ''
  } else if (date === undefined) { //applications section
    sectionBody = ` ${title} ${description}`
  } else { // experience and education
    sectionBody = ` ${title} ${date}, ${description}`
  }

  console.log(sectionBody)
  return (
    <Section>
      <div>{sectionBody}</div>
      <div>{details}</div>
    </Section>
  )
}

CvSectionItem.propTypes = {
  // title: PropTypes.string,
  // description: PropTypes.string,
  // date: PropTypes.string,
  // details: PropTypes.string,
}

CvSectionItem.defaultProps = {
  // title: 'title-missing', 
  // description: 'description-missing', 
  // date: 'date-missing', 
  // details: 'details-missing',
}

export default CvSectionItem
